export default {
	props: {
		current: {
			type: Number|String,
			default: 0
		}
	},
	data() {
		return {
			pages: getCurrentPages()
			// current:1,
		};
	},
	created() {},
	methods: {
		click(i = 0) {
			let url = 'pages/cosmos/index';

			switch (i) {
				case 1:
					url = 'pages/list/chat';
					break;
			}
			let pages = this.pages;
			let le = pages.length-1;
			let back = -1;
			let x = 0;
			console.log('pages.length',pages.length);
			console.log(pages);
			for (let s = le; s >= 0; s--) {
				console.log('sss', s);
				console.log('当前循环route',pages[s].route)
				if (url == pages[s].route) {
					back = x;
					s = -98;
				}
				x ++
				if(s == -99){
					return
				}
				
			}
			console.log('basck', back);
			// return

			switch (back) {
				case -1:
					console.log('前往新页面');
					uni.navigateTo({
						url:'/'+url
					});
					break;
				case 0:
					console.log('无需处理');
					break;
				default:
				uni.navigateBack({
					delta: x
					})
					break;
			}
			console.log('i', i);
		}
	}
};
